@import "./vars.scss";

.toggle {
  display: none;

  @media screen and (max-width: $screen-width-desktop) {
    display: flex;
    position: absolute;
    right: 0;
    top: 13px;
  }

  &__menu {
    opacity: 0;
    position: absolute;
  }

  &__btn {
    display: block;
    position: relative;
    width: 40px;
    height: 30px;
    cursor: pointer;
    z-index: 1;
  }

