  &__span {
    display: block;
    position: absolute;
    width: 100%;
    height: 4px;
    background-color: #fff;
    border-radius: 4px;
    transition-duration: 0.2s;
  }

  &__span::before {
    content: "";
    top: -8px;
    @extend %toggle-span-style;
  }

  &__span::after {
    content: "";
    top: 8px;
    @extend %toggle-span-style;
  }

  &__menu:checked ~ .toggle__btn > .toggle__span {
    transform: rotate(45deg);
  }

  &__menu:checked ~ .toggle__btn > .toggle__span::before {
    top: 0;
    transform: rotate(0);
  }

  &__menu:checked ~ .toggle__btn > .toggle__span::after {
    top: 0;
    transform: rotate(90deg);
  }

